.plus,
.menu .title {
  background: url(../dist/components/styles/img/plus.svg);
}
.minus,
.menu.open .title {
  background-image: url(../dist/components/styles/img/minus.svg);
}
.menu .title {
  background-position: left 1px;
  background-repeat: no-repeat;
  padding: 0 6px 0 16px;
  background-size: 14px;
  font-weight: bold;
  cursor: pointer;
}
.menu .list {
  display: none;
  margin: 0;
}
.menu .list a {
  color: blue;
}
.menu.open .list {
  display: block;
}
